.match-court {
  color: #666666;
  margin-bottom: 20px;
  font-size: 1rem;
}

.match-finished-notice {
  background: #d4edda;
  color: #155724;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.match-management {
  margin-top: 20px;
}

.games-container {
  margin-bottom: 20px;
}

.no-games-notice {
  text-align: center;
  padding: 40px 20px;
  color: #666666;
  background: #f8f9fa;
  border-radius: 8px;
}

.add-game-btn {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.add-game-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(40, 167, 69, 0.4);
}

.games-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.game-input {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 20px;
  border: 1px solid #e9ecef;
}

.game-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.game-header label {
  font-weight: 600;
  color: #333333;
  font-size: 1.1rem;
}

.game-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.remove-game {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px rgba(231, 76, 60, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.correct-game-score {
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 3px 8px rgba(243, 156, 18, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.correct-game-score:hover {
  background: linear-gradient(135deg, #e67e22 0%, #f39c12 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(243, 156, 18, 0.6);
  border-color: rgba(255, 255, 255, 0.4);
}

.remove-game:hover {
  background: linear-gradient(135deg, #c0392b 0%, #e74c3c 100%);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(231, 76, 60, 0.6);
  border-color: rgba(255, 255, 255, 0.4);
}

.tiebreak-input,
.normal-game {
  margin-bottom: 15px;
}

.tiebreak-input label,
.normal-game label {
  display: block;
  margin-bottom: 10px;
  font-weight: 600;
  color: #333333;
}

.score-inputs {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.score-inputs input {
  width: 80px;
  padding: 10px;
  border: 2px solid #e9ecef;
  border-radius: 6px;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 600;
}

.score-inputs input:focus {
  outline: none;
  border-color: #3b82f6;
}

.score-separator {
  font-size: 1.2rem;
  font-weight: 700;
  color: #333333;
}

.toggle-tiebreak {
  background: #6c757d;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.toggle-tiebreak:hover {
  background: #5a6268;
}

.match-actions {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.add-game,
.finish-match {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.add-game {
  background: linear-gradient(135deg, #17a2b8 0%, #20c997 100%);
  color: white;
}

.add-game:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(23, 162, 184, 0.4);
}

.finish-match {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  color: white;
}

.finish-match:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(40, 167, 69, 0.4);
}

.correct-score {
  background: linear-gradient(135deg, #ffc107 0%, #ff9800 100%);
  color: #333;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.3s ease;
  min-width: 150px;
}

.correct-score:hover {
  background: linear-gradient(135deg, #ff9800 0%, #f57c00 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 152, 0, 0.4);
}

.recalculate-stats {
  background: linear-gradient(135deg, #4ecdc4 0%, #44a08d 100%);
  color: #333;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.3s ease;
  min-width: 150px;
}

.recalculate-stats:hover {
  background: linear-gradient(135deg, #44a08d 0%, #3a7a6b 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(78, 205, 196, 0.4);
}

/* Standings */
.standings h3 {
  color: #333333;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.standings-header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 15px;
}

.table-container {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: white;
}

th {
  padding: 15px;
  text-align: left;
  font-weight: 600;
  font-size: 1rem;
}

tbody tr {
  border-bottom: 1px solid #e9ecef;
  transition: background-color 0.3s ease;
}

tbody tr:hover {
  background-color: #f8f9fa;
}

tbody tr:last-child {
  border-bottom: none;
}

td {
  padding: 15px;
  font-size: 1rem;
}

.position {
  font-weight: 700;
  text-align: center;
  width: 60px;
}

.pair-name {
  font-weight: 600;
  color: #333333;
}

.points {
  font-weight: 700;
  color: #3b82f6;
  text-align: center;
}

/* Position colors */
.position-1 {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
}

.position-2 {
  background: linear-gradient(135deg, #c0c0c0 0%, #e8e8e8 100%);
}

.position-3 {
  background: linear-gradient(135deg, #cd7f32 0%, #daa520 100%);
}

