/* Optimizaciones globales para móvil */
@media (max-width: 768px) {
  /* Mejorar botones para pantallas táctiles */
  button {
    -webkit-tap-highlight-color: transparent !important;
    touch-action: manipulation !important;
    -webkit-user-select: none !important;
    -moz-user-select: none !important;
    -ms-user-select: none !important;
    user-select: none !important;
    /* Tamaño mínimo para dedos */
    min-height: 44px !important;
    min-width: 44px !important;
  }

  /* Mejorar inputs para móvil */
  input,
  textarea,
  select {
    font-size: 16px !important; /* Prevenir zoom en iOS */
    -webkit-appearance: none !important;
    border-radius: 0 !important;
  }

  /* Mejorar scroll en móvil */
  .left-panel,
  .right-panel {
    -webkit-overflow-scrolling: touch !important;
    scroll-behavior: smooth !important;
  }

  /* Optimizar animaciones para móvil */
  * {
    -webkit-transform: translateZ(0) !important;
    transform: translateZ(0) !important;
  }

  /* Mejorar rendimiento de hover en móvil */
  .pair-display-card:hover,
  .match-card-new:hover,
  .modern-tournament-card:hover {
    transform: none !important;
  }

  /* Optimizar grid para móvil */
  .pairs-grid,
  .matches-container,
  .players-grid {
    grid-template-columns: 1fr !important;
  }

  /* Mejorar espaciado en formularios */
  .form-group {
    margin-bottom: 12px !important;
  }

  .form-group label {
    display: block !important;
    margin-bottom: 4px !important;
    font-size: 0.9rem !important;
  }

  .form-group input,
  .form-group textarea {
    width: 100% !important;
    padding: 8px 12px !important;
    border: 1px solid #e2e8f0 !important;
    border-radius: 6px !important;
    font-size: 16px !important;
  }

  /* Mejorar modales en móvil */
  .modal-overlay {
    padding: 10px !important;
  }

  .modal-content {
    max-width: 95% !important;
    max-height: 90vh !important;
    overflow-y: auto !important;
  }

  /* Optimizar tablas para móvil */
  .standings-table {
    font-size: 0.8rem !important;
  }

  .standings-table th,
  .standings-table td {
    padding: 6px 4px !important;
  }

  /* Mejorar navegación en móvil */
  .tournament-manager {
    margin-bottom: 20px !important;
  }

  .tournaments-grid {
    gap: 10px !important;
  }

  /* Optimizar iconos para móvil */
  .tournament-icon,
  .modern-title-icon,
  .modern-status-icon {
    font-size: 1.2rem !important;
  }
}

/* Winner Button */
.winner-button-container {
  text-align: center;
  margin: 30px 0;
}

.show-winner-button {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 50%, #ffb347 100%);
  color: #000000;
  border: none;
  padding: 28px 56px;
  border-radius: 24px;
  font-size: 1.5rem;
  font-weight: 800;
  cursor: pointer;
  box-shadow: 0 15px 50px rgba(255, 215, 0, 0.4);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: winnerButtonPulse 2s ease-in-out infinite;
  backdrop-filter: blur(15px);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

@keyframes winnerButtonPulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.show-winner-button:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 20px 60px rgba(255, 215, 0, 0.6);
  background: linear-gradient(135deg, #ffed4e 0%, #ffb347 50%, #ff8c00 100%);
}

/* Botón Volver al Inicio */
.back-home-button-container {
  margin: 20px 0;
  text-align: center;
}

.back-home-button {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 50%, #ffb347 100%);
  color: #000000;
  border: none;
  padding: 20px 40px;
  border-radius: 32px;
  font-size: 1.3rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 10px 30px rgba(255, 215, 0, 0.4);
  animation: backHomeButtonPulse 2s ease-in-out infinite;
  backdrop-filter: blur(15px);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

@keyframes backHomeButtonPulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}

.back-home-button:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 15px 40px rgba(255, 215, 0, 0.6);
  background: linear-gradient(135deg, #ffed4e 0%, #ffb347 50%, #ff8c00 100%);
}

/* Contenedor de acciones del partido */
.match-actions {
