/* Winner Screen - DISEÑO LLAMATIVO Y ELEGANTE */
.winner-screen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  background: linear-gradient(
    135deg,
    #0f172a 0%,
    #1e293b 20%,
    #334155 40%,
    #475569 60%,
    #64748b 80%,
    #94a3b8 100%
  ) !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  z-index: 1000 !important;
  animation: winnerFadeIn 1s ease-out !important;
  backdrop-filter: blur(25px) !important;
  overflow: hidden !important;
}

.winner-screen::before {
  content: "" !important;
  position: absolute !important;
  top: -50% !important;
  left: -50% !important;
  width: 200% !important;
  height: 200% !important;
  background: radial-gradient(
    circle,
    rgba(14, 165, 233, 0.1) 0%,
    transparent 70%
  ) !important;
  animation: rotate 20s linear infinite !important;
}

.winner-screen::after {
  content: "" !important;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(16, 185, 129, 0.05) 50%,
    transparent 70%
  ) !important;
  animation: shimmer 3s ease-in-out infinite !important;
}

@keyframes winnerFadeIn {
  from {
    opacity: 0;
    transform: scale(0.8) rotate(-5deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes shimmer {
  0%,
  100% {
    opacity: 0.3;
    transform: translateX(-100%);
  }
  50% {
    opacity: 0.8;
    transform: translateX(100%);
  }
}

@keyframes contentFloat {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

@keyframes colorShift {
  0%,
  100% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(30deg);
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 0.1;
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    opacity: 0.3;
    transform: translate(-50%, -50%) scale(1.1);
  }
}

@keyframes trophyBounce {
  0%,
  100% {
    transform: translateY(-50%) scale(1);
  }
  50% {
    transform: translateY(-50%) scale(1.2);
  }
}

@keyframes nameGlow {
  0%,
  100% {
    filter: drop-shadow(0 1px 2px rgba(255, 255, 255, 0.3));
  }
  50% {
    filter: drop-shadow(0 1px 2px rgba(255, 255, 255, 0.6))
      drop-shadow(0 0 10px rgba(14, 165, 233, 0.3));
  }
}

@keyframes statPulse {
  0%,
  100% {
    transform: scale(1);
    box-shadow: 0 12px 25px rgba(14, 165, 233, 0.4),
      0 0 30px rgba(14, 165, 233, 0.2);
  }
  50% {
    transform: scale(1.02);
    box-shadow: 0 15px 30px rgba(14, 165, 233, 0.5),
      0 0 40px rgba(14, 165, 233, 0.3);
  }
}

@keyframes numberGlow {
  0%,
  100% {
    filter: drop-shadow(0 1px 2px rgba(255, 255, 255, 0.2));
  }
  50% {
    filter: drop-shadow(0 1px 2px rgba(255, 255, 255, 0.4))
      drop-shadow(0 0 8px rgba(255, 255, 255, 0.3));
  }
}

@keyframes buttonPulse {
  0%,
  100% {
    box-shadow: 0 8px 20px rgba(16, 185, 129, 0.4),
      0 0 30px rgba(16, 185, 129, 0.2);
  }
  50% {
    box-shadow: 0 10px 25px rgba(16, 185, 129, 0.5),
      0 0 40px rgba(16, 185, 129, 0.3);
  }
}

@keyframes houseBounce {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-2px);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

