/* Nuevo Editor de Marcador - Diseño Elegante */
.score-editor-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 16px;
}

.score-editor-modal {
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  max-width: 90vw;
  max-height: 90vh;
  width: 520px;
  overflow: hidden;
  animation: scoreEditorSlideIn 0.4s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes scoreEditorSlideIn {
  from {
    opacity: 0;
    transform: translateY(-30px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.score-editor-header {
  background: #1a1a1a;
  color: white;
  padding: 24px 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #333;
}

.score-editor-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.score-editor-title h3 {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: -0.025em;
}

.score-editor-icon {
  font-size: 1.6rem;
  opacity: 0.9;
}

.score-editor-close {
  background: transparent;
  border: 1px solid #444;
  color: #ccc;
  width: 40px;
  height: 40px;
  border-radius: 12px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.score-editor-close:hover {
  background: #333;
  border-color: #555;
  color: white;
  transform: scale(1.05);
}

.score-editor-content {
  padding: 32px;
  max-height: calc(90vh - 120px);
  overflow-y: auto;
}

.score-editor-match-info {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 16px;
  padding: 24px;
  margin-bottom: 24px;
}

.score-editor-match-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.score-editor-pairs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.score-editor-pair {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 16px;
  text-align: center;
}

.score-editor-pair-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #64748b;
  margin-bottom: 8px;
}

.score-editor-pair-names {
  font-size: 1rem;
  font-weight: 600;
  color: #1e293b;
}

.score-editor-games-section {
  margin-bottom: 24px;
}

.score-editor-games-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.score-editor-games-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1e293b;
  display: flex;
  align-items: center;
  gap: 8px;
}

.score-editor-add-game {
  background: #10b981;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 10px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.score-editor-add-game:hover {
  background: #059669;
  transform: translateY(-1px);
}

.score-editor-games-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.score-editor-game {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  position: relative;
}

.score-editor-game-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid #f1f5f9;
}

.score-editor-game-title {
  font-size: 0.9rem;
  font-weight: 600;
  color: #374151;
  background: #f3f4f6;
  padding: 4px 8px;
  border-radius: 6px;
}

.score-editor-game-actions {
  display: flex;
  gap: 6px;
}

.score-editor-remove-game {
  background: #fee2e2;
  color: #dc2626;
  border: 1px solid #fecaca;
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 0.7rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 3px;
}

.score-editor-remove-game:hover {
  background: #fecaca;
  border-color: #fca5a5;
  transform: scale(1.02);
}

.score-editor-score-container {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  gap: 12px;
  padding: 8px 0;
}

.score-editor-score-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.score-editor-pair-name {
  font-size: 0.8rem;
  font-weight: 500;
  color: #6b7280;
  text-align: center;
  line-height: 1.2;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.score-editor-input {
  width: 60px;
  height: 50px;
  padding: 8px;
  border: 2px solid #d1d5db;
  border-radius: 8px;
  font-size: 1.25rem;
  font-weight: 700;
  text-align: center;
  transition: all 0.2s ease;
  background: #f9fafb;
  color: #1f2937;
}

.score-editor-input:focus {
  outline: none;
  border-color: #3b82f6;
  background: white;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  transform: scale(1.02);
}

.score-editor-separator {
  font-size: 1.25rem;
  font-weight: 700;
  color: #9ca3af;
  padding: 0 4px;
  user-select: none;
}

.score-editor-error {
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 12px;
  padding: 16px;
  color: #dc2626;
  font-size: 0.875rem;
  margin-bottom: 16px;
}

.score-editor-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 24px;
}

.score-editor-finish,
.score-editor-cancel {
  padding: 14px 24px;
  border: none;
  border-radius: 12px;
  font-size: 0.875rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.score-editor-finish {
  background: #f59e0b;
  color: white;
}

.score-editor-finish:hover:not(:disabled) {
  background: #d97706;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}

.score-editor-finish:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.score-editor-cancel {
  background: #6b7280;
  color: white;
}

.score-editor-cancel:hover {
  background: #4b5563;
  transform: translateY(-2px);
}

/* Responsive */
@media (max-width: 768px) {
  .score-editor-modal {
    width: 95vw;
    max-height: 95vh;
  }

  .score-editor-content {
    padding: 24px;
  }

  .score-editor-header {
    padding: 20px 24px;
  }

  .score-editor-title h3 {
    font-size: 1.2rem;
  }

  .score-editor-pairs {
    grid-template-columns: 1fr;
    gap: 12px;
  }

  .score-editor-score-container {
    grid-template-columns: 1fr auto 1fr;
    gap: 8px;
  }

  .score-editor-input {
    width: 50px;
    height: 45px;
    font-size: 1.1rem;
  }

  .score-editor-pair-name {
    font-size: 0.75rem;
  }

  .score-editor-separator {
    font-size: 1.1rem;
    padding: 0 2px;
  }

  .score-editor-actions {
    flex-direction: column;
  }

  .score-editor-finish,
  .score-editor-cancel {
    width: 100%;
  }
}

