  font-size: 0.9rem;
}

.point-input input {
  padding: 8px 12px;
  border: 2px solid #e9ecef;
  border-radius: 6px;
  font-size: 1rem;
  text-align: center;
  transition: border-color 0.3s ease;
}

.point-input input:focus {
  outline: none;
  border-color: #ffd700;
}

.tie-break-info,
.game-info {
  background: #e3f2fd;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 15px;
}

.tie-break-info p,
.game-info p {
