.tie-explanation h6 {
  margin: 0 0 10px 0;
  color: #2e7d32;
  font-size: 0.9rem;
}

.tie-explanation ul {
  margin: 0;
  padding-left: 20px;
}

.tie-explanation li {
  margin: 5px 0;
  font-size: 0.85rem;
  color: #2e7d32;
  line-height: 1.4;
}

