  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.1);
}

.debug-toggle-btn {
  width: 100%;
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: white;
  border: none;
  padding: 8px 15px;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
