  transform: translateY(-1px);
}

.debug-info {
  background: #f8f9fa;
  padding: 15px;
  border-top: 1px solid #e9ecef;
}

.debug-header {
  margin-bottom: 15px;
}

.debug-header h4 {
  color: #333;
  margin-bottom: 10px;
  font-size: 1rem;
  font-weight: 600;
}

.debug-stats {
