.debug-btn {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px rgba(14, 165, 233, 0.3);
}

