/* ===== MODERN PLAYER MANAGER 2025 ===== */

.modern-player-manager {
  background: linear-gradient(
    135deg,
    rgba(0, 0, 0, 0.95) 0%,
    rgba(45, 45, 45, 0.95) 100%
  );
  border-radius: 24px;
  padding: 32px;
  border: 2px solid #ffd700;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3), 0 8px 32px rgba(255, 215, 0, 0.2);
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
}

.modern-player-manager::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #ffd700, #ffed4e, #ffb347, #ffd700);
  animation: shimmer 3s ease-in-out infinite;
}

/* Header Moderno */
.modern-player-header {
  margin-bottom: 32px;
  position: relative;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 16px;
}

.title-icon {
  font-size: 2rem;
  filter: drop-shadow(0 2px 8px rgba(255, 215, 0, 0.4));
  animation: iconFloat 3s ease-in-out infinite;
}

.header-title h3 {
  color: #ffd700;
  font-size: 1.8rem;
  font-weight: 800;
  margin: 0;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.player-count {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 1.1rem;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
  animation: countPulse 2s ease-in-out infinite;
}

/* Botón Agregar Moderno */
.modern-add-btn {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  border: none;
  padding: 12px 24px;
  border-radius: 16px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 6px 20px rgba(255, 215, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  overflow: hidden;
}

.modern-add-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  transition: left 0.6s ease;
}

.modern-add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.4);
  background: linear-gradient(135deg, #ffed4e 0%, #ffb347 100%);
}

.modern-add-btn:hover::before {
  left: 100%;
}

.btn-icon {
  font-size: 1.2rem;
  font-weight: 700;
}

.btn-text {
  font-weight: 600;
}

/* Formulario Moderno */
.modern-create-form {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 32px;
  margin-bottom: 32px;
  border: 1px solid rgba(255, 215, 0, 0.3);
  backdrop-filter: blur(15px);
  animation: slideDown 0.4s ease-out;
}

.form-header {
  text-align: center;
  margin-bottom: 24px;
}

.form-header h4 {
  color: #ffd700;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 8px 0;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
}

.form-header p {
  color: #ffffff;
  font-size: 1rem;
  margin: 0;
  opacity: 0.8;
}

.modern-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.modern-input-group {
  position: relative;
}

.modern-input {
  width: 100%;
  padding: 16px 20px;
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 16px;
  background: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  font-size: 1rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.modern-input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.modern-input:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 0 4px rgba(255, 215, 0, 0.2);
  background: rgba(0, 0, 0, 0.8);
}

.input-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #ffd700, #ffed4e);
  transition: width 0.3s ease;
}

.modern-input:focus + .input-border {
  width: 100%;
}

.form-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
}

.modern-submit-btn,
.modern-cancel-btn {
  padding: 12px 24px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  overflow: hidden;
}

.modern-submit-btn {
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
}

.modern-submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 215, 0, 0.4);
  background: linear-gradient(135deg, #ffed4e 0%, #ffb347 100%);
}

.modern-cancel-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.modern-cancel-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

/* Mensaje de Error */
.modern-error {
  background: rgba(255, 68, 68, 0.2);
  border: 1px solid rgba(255, 68, 68, 0.4);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: #ffffff;
  animation: shake 0.5s ease-in-out;
}

.error-icon {
  font-size: 1.2rem;
}

/* Estado Vacío */
.modern-empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #ffffff;
}

.empty-icon {
  font-size: 4rem;
  margin-bottom: 16px;
  opacity: 0.6;
  animation: float 3s ease-in-out infinite;
}

.modern-empty-state h4 {
  color: #ffd700;
  font-size: 1.5rem;
  margin: 0 0 8px 0;
  font-weight: 700;
}

.modern-empty-state p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  margin: 0;
}

/* Grid de Jugadores */
.modern-players-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 24px;
}

/* Tarjeta de Jugador Moderna */
.modern-player-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  padding: 24px;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  border: 2px solid #ffd700;
  backdrop-filter: blur(15px);
}

.modern-player-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #ffd700, #ffed4e, #ffb347);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.modern-player-card:hover::before {
  transform: scaleX(1);
}

.modern-player-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 8px 32px rgba(255, 215, 0, 0.2);
  border-color: rgba(255, 215, 0, 0.5);
}

.card-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(255, 215, 0, 0.05) 0%,
    rgba(255, 237, 78, 0.05) 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modern-player-card:hover .card-background {
  opacity: 1;
}

.player-content {
  display: flex;
  align-items: center;
  gap: 16px;
  position: relative;
  z-index: 2;
}

.player-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
  transition: all 0.3s ease;
}

.modern-player-card:hover .player-avatar {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(255, 215, 0, 0.4);
}

.player-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.player-name {
  color: #1a1a1a;
  font-weight: 700;
  font-size: 1.2rem;
  text-shadow: none;
  letter-spacing: 0.5px;
}

.pair-indicator {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #cc6600;
  font-size: 0.9rem;
  font-weight: 600;
  text-shadow: none;
}

.indicator-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #cc6600;
  animation: pulse 2s ease-in-out infinite;
}

/* Botón Eliminar Moderno */
.modern-delete-btn {
  background: rgba(255, 68, 68, 0.2);
  border: 1px solid rgba(255, 68, 68, 0.4);
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: scale(0.8);
}

.modern-player-card:hover .modern-delete-btn {
  opacity: 1;
  transform: scale(1);
}

.modern-delete-btn:hover {
  background: rgba(255, 68, 68, 0.4);
  transform: scale(1.1);
}

.delete-icon {
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 700;
}

/* Indicador de Selección */
.selection-indicator {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: bounceIn 0.6s ease-out;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.4);
}

.check-icon {
  color: #000000;
  font-weight: 700;
  font-size: 0.9rem;
}

/* Estados de la Tarjeta */
.modern-player-card.selected {
  border-color: #ffd700;
  background: rgba(255, 215, 0, 0.2);
  box-shadow: 0 12px 32px rgba(255, 215, 0, 0.4);
}

.modern-player-card.in-pair {
  border-color: #cc6600;
  background: rgba(204, 102, 0, 0.15);
}

.modern-player-card.in-pair .player-avatar {
  background: linear-gradient(135deg, #cc6600 0%, #b8590c 100%);
}

/* Efectos de Partículas */
.particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ffd700;
  border-radius: 50%;
  opacity: 0;
  animation: particleFloat 3s ease-in-out infinite;
}

.particle:nth-child(1) {
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.particle:nth-child(2) {
  top: 60%;
  right: 15%;
  animation-delay: 1s;
}

.particle:nth-child(3) {
  bottom: 30%;
  left: 20%;
  animation-delay: 2s;
}

/* Loading Moderno */
.modern-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  color: #ffffff;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 215, 0, 0.3);
  border-top: 3px solid #ffd700;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

.modern-loading p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1rem;
  margin: 0;
}

/* ===== ANIMACIONES 2025 ===== */

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes iconFloat {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

@keyframes countPulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.2);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes particleFloat {
  0% {
    opacity: 0;
    transform: translateY(0px) scale(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-20px) scale(1);
  }
  100% {
    opacity: 0;
    transform: translateY(-40px) scale(0);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Responsive Modern Player Manager */
@media (max-width: 768px) {
  .modern-players-grid {
    grid-template-columns: 1fr;
  }

  .header-content {
    flex-direction: column;
