  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  padding: 6px 12px;
  border-radius: 16px;
  font-weight: 700;
  font-size: 1rem;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
  animation: countPulse 2s ease-in-out infinite;
}

/* Grid de Parejas Mejorado */
.new-pairs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

/* Tarjeta de Pareja Nueva */
.new-pair-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 24px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  border: 2px solid rgba(255, 215, 0, 0.3);
  backdrop-filter: blur(10px);
  min-height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.new-card-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(255, 215, 0, 0.05) 0%,
    rgba(255, 179, 71, 0.05) 100%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.new-pair-card:hover .new-card-background {
  opacity: 1;
}

.new-pair-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #ffd700, #ffed4e, #ffb347);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.new-pair-card:hover::before {
  transform: scaleX(1);
}

.new-pair-card:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.2), 0 8px 24px rgba(255, 215, 0, 0.2);
  border-color: rgba(255, 215, 0, 0.6);
}

/* Contenido de la Pareja */
.new-pair-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.new-pair-players {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 100%;
}

.new-player-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  flex: 1;
}

.new-player-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ffd700 0%, #ffed4e 100%);
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
  transition: all 0.3s ease;
  position: relative;
}

.new-player-avatar::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ffd700, #ffed4e, #ffb347);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.new-pair-card:hover .new-player-avatar::after {
  opacity: 1;
}

.new-player-name {
  color: #1a1a1a;
  font-weight: 600;
  font-size: 1rem;
  text-align: center;
  text-shadow: 0 1px 3px rgba(255, 255, 255, 0.8);
}

.new-vs-badge {
  background: linear-gradient(135deg, #ffb347 0%, #ff8c00 100%);
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 16px;
  font-weight: 700;
  font-size: 0.9rem;
  box-shadow: 0 4px 12px rgba(255, 179, 71, 0.3);
  animation: pulse 2s ease-in-out infinite;
  position: relative;
  z-index: 1;
}

.new-vs-badge::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  border-radius: 16px;
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.new-pair-card:hover .new-vs-badge::before {
  opacity: 1;
}

/* Botón de Eliminar */
.new-delete-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  background: linear-gradient(135deg, #ff4444 0%, #cc0000 100%);
  color: #ffffff;
  box-shadow: 0 3px 10px rgba(255, 68, 68, 0.3);
  opacity: 0;
  transform: scale(0.8);
}

.new-pair-card:hover .new-delete-btn {
  opacity: 1;
  transform: scale(1);
}

.new-delete-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 5px 15px rgba(255, 68, 68, 0.4);
  background: linear-gradient(135deg, #ff6666 0%, #ff0000 100%);
}

.new-delete-icon {
  transition: transform 0.2s ease;
}

.new-delete-btn:hover .new-delete-icon {
  transform: scale(1.1);
}

/* Estados Vacíos */
.new-empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #ffffff;
}

.new-empty-icon {
  font-size: 3rem;
  margin-bottom: 16px;
  animation: float 3s ease-in-out infinite;
}

.new-empty-state h4 {
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0 8px 0;
  color: #ffd700;
}

.new-empty-state p {
  font-size: 1rem;
  opacity: 0.8;
  margin: 0;
}

/* Mensaje de Error */
.new-error {
  background: rgba(255, 68, 68, 0.1);
  border: 1px solid rgba(255, 68, 68, 0.3);
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  color: #ff4444;
  animation: shake 0.5s ease-in-out;
}

.new-error-icon {
  font-size: 1.2rem;
}

/* Loading */
.new-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  color: #ffffff;
}

.new-loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 215, 0, 0.3);
  border-top: 3px solid #ffd700;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

.new-loading p {
  font-size: 1rem;
  opacity: 0.8;
  margin: 0;
}

/* Efectos de Partículas */
.new-particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  overflow: hidden;
}

.new-particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: #ffd700;
  border-radius: 50%;
  opacity: 0;
  animation: newParticleFloat 4s ease-in-out infinite;
}

.new-particle:nth-child(1) {
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.new-particle:nth-child(2) {
  top: 60%;
  right: 15%;
  animation-delay: 1.5s;
}

.new-particle:nth-child(3) {
  bottom: 30%;
  left: 20%;
  animation-delay: 3s;
}

/* Animaciones Nuevas */
@keyframes newParticleFloat {
  0%,
  100% {
    opacity: 0;
    transform: translateY(0) scale(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-20px) scale(1);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .new-pair-manager {
    padding: 16px;
    margin: 0 8px;
  }

  .new-pairs-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .new-pair-players {
    gap: 16px;
  }

  .new-player-avatar {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .new-player-name {
    font-size: 0.9rem;
  }

  .new-vs-badge {
    padding: 6px 12px;
    font-size: 0.8rem;
  }

  .new-pair-card {
    padding: 20px;
    min-height: 120px;
  }
}

